.paginate-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 80px;
  margin-top: 20px;
}

.paginationBttns {
  width: 80%;
  height: 40px;
  list-style: none;
  display: flex;
  justify-content: center;
}

.paginationBttns a {
  padding: 10px;
  margin: 8px;
  border-radius: 20px;
  border: 1px solid #e5890a;
  background-color: #f7d08a;
  color: #fafafa;
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
}

.paginationBttns a:hover {
  background-color: #9d5c0d;
  color: #ffeddb;
}

.paginationActive a {
  background-color: #9d5c0d;
  color: #ffeddb;
}

.paginationDisabled a {
  background-color: #ffeddb;
  color: #9d5c0d;
  opacity: .6;
}
.paginationDisabled a:hover{
  background-color: #ffeddb;
  color: #9d5c0d;
  
}
